import React, { useEffect, useState } from 'react';
import axios from 'axios';
const Result = (props) => {
  const addStudent = () => {
    axios
      .post('/class/addstudent', { className: props.className })
      .then((res) => {
        console.log(res);
      });
  };
  const [profileImg, setProfileImg] = useState();

  const getProfileImage = async () => {
    await axios
      .post(
        `/contact/contactprofileimage`,
        { userName: props.userName },
        { responseType: 'arraybuffer' }
      )
      .then((resp) => {
        let base64ImageString = Buffer.from(
          resp.data,
          'binary'
        ).toString('base64');
        setProfileImg('data:image/png;base64,' + base64ImageString);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getProfileImage();
  });
  return (
    <div className="class-result">
      <div>
        <img src={profileImg} className="profile" />
        <div>
          <h3 className="name">{props.className}</h3>
          <p className="module">{props.name}</p>
        </div>
      </div>
      <button className="btn" onClick={addStudent}>
        Add
      </button>
    </div>
  );
};

export default Result;
