import React, { useRef } from 'react';
import search from '../../assets/search.svg';
import { useState } from 'react';
import Result from './Result';
import profprofile from '../../assets/profprofile.png';
import axios from 'axios';
const JoinExpand = () => {
  axios.defaults.withCredentials = true;
  const [onSearch, setOnSearch] = useState(false);
  const [searchResult, setSearchResult] = useState();
  let classCode = useRef(null);
  const searchClass = () => {
    axios
      .post('/class/searchclass', {
        className: classCode.current.value,
      })
      .then((res) => {
        setSearchResult(res.data.result);
      });
  };
  const assStudent = () => {
    axios
      .post('/class/addstudent', {
        classCode: classCode.current.value,
      })
      .then((res) => {
        console.log(res);
      });
  };
  return (
    <div className="join-expand">
      <div className="search-box">
        <input
          ref={classCode}
          placeholder="Enter ClassName"
          className="input"
          onKeyUp={(e) => {
            if (e.key == 'Enter') {
              e.preventDefault();
              setOnSearch(true);
              searchClass();
            }
          }}
        />
        <img
          src={search}
          onClick={() => {
            setOnSearch(true);
            searchClass();
          }}
          className="search-icon"
        />
      </div>

      {searchResult && (
        <Result
          name={searchResult.firstName + searchResult.lastName}
          className={searchResult.className}
          userName={searchResult.userName}
        />
      )}
    </div>
  );
};

export default JoinExpand;
