import React,{useEffect} from 'react'
import Course from './GlobalSubCompenents/Course'
import profprofile from '../assets/profprofile.png'
import './styles/learning/learningstyle.css'
import { useState } from 'react'
import JoinExpand from './learningSubCompenents/JoinExpand'
import axios from 'axios'
import Module from './learningSubCompenents/Module'
import Result from './learningSubCompenents/Result'

const Learning = () => {
  axios.defaults.withCredentials = true
  const [onJoin, setOnJoin] = useState(false);
  const [classes, setClasses] = useState([])
  const getClasses = ()=>{
    axios.get("/class/allstudentclasses").then((res)=>{
      setClasses(res.data.result)
  })
}
useEffect(() => {
  getClasses()

}, [])

  return (
    <div className='whole-pg'>
      <div className='flexit'>
        <h5 className='heading'>Learning</h5>
        <p className='join' onClick={()=>{setOnJoin(!onJoin)}} >{onJoin? 'Hide' : 'Join a class +'}</p>
      </div>
      {onJoin  &&  <JoinExpand /> }
      <div className='postes'>
      {
        classes.map((Class)=>{
          return <Module module={Class.className} code={Class.classCode} link={`${Class.className}`}/>
        })
      }
      </div>
    </div>
  )
}

export default Learning